
import http from "@/axios"
import store from "@/store"
import { mapState } from 'vuex'
import Vue from "vue"
import native from "@/native"
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import dialogQuestionnaire from '../components/dialog_questionnaire'

export default {
name:"commit_offline_activity",
data(){
return{
dialogShow:false,
result:{},
}
},
computed:{
...mapState({
picture: (state) => {
if(state.activityModule.picture){
return state.activityModule.picture
}else{
return null
}

}
})
},
methods:{
takePhoto(){
try {
native.method("takePhoto",function (result) {
console.log("调用成功");
})
}catch (e){
alert(e);
}

},
deleteImg(){
store.commit("OFFLINE_IMG",{picture:null});
},
cancelActivity(){
var self = this;
http.cancelActivity(this.$route.query.id,(response)=>{
Vue.$vux.toast.show({text:"取消成功"});
self.$router.go(-1);
store.commit('UPDATE_ACTIVITY_STATUS', {activityStatus:5})
})
},
commitChildActivity(){
if(!this.picture){
Vue.$vux.toast.show({text:"请上传活动图片",type:"cancel"});
return;
}
var self = this;

http.commitChildActivity(this.$route.query.id,{
seriesId:this.$route.query.childId,

deliverImage1 :this.picture ,
questionStatus :"2" ,
lastOne:this.$route.query.lastOne
},(response)=>{
Vue.$vux.toast.show({text:"提交成功"});
self.$router.go(-1);
if(this.$route.query.lastOne==1){
store.commit('UPDATE_STATUS')
}else{
store.commit('UPDATE_STATUS')
}
})
},
commitActivity(){
if(!this.picture){
Vue.$vux.toast.show({text:"请上传活动图片",type:"cancel"});
return;
}

if(this.result.questionStatus == 0 && this.result.questionId){
var self = this;
http.activityDetail(this.$route.query.id,function(response) {
self.result = response.data;
this.dialogShow = true;
})
}else{
this.commitActivity2();
}
},
commitActivity2(){
var self = this;
http.submitActivity(this.$route.query.id,{
activityForm :0,

deliverImage1 :this.picture ,
questionStatus :"2" ,
},function (response) {
Vue.$vux.toast.show({text:"提交成功"});
self.$router.go(-1);
store.commit('UPDATE_ACTIVITY_STATUS', {activityStatus:1})
});
},
closeDialog(){
this.dialogShow = false;
}
},
directives: {
TransferDom
},
components:{
XDialog,dialogQuestionnaire
},
}



